section {
    background: #1951fe;
    font-family: 'Poppins', sans-serif;
    width: 100%;
    height: 100vh;
    background-size: cover;
}

section .wave {
    position: absolute;
    width: 100%;
    height: 143px;
    bottom: 0;
    left: 0;
    background: url("../../image/wave.png");
    animation: animate 10s backwards infinite;
}

@keyframes animate {
    0% {
        background-position: 0;
    }
    100% {
        background-position: 1360px;
    }
}

section .wave:before {
    content: '';
    width: 100%;
    height: 143px;
    background: url("../../image/wave.png");
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.5;
    animation-delay: 4s;
    animation: animate-reverse 10s linear infinite;
}

@keyframes animate-reverse {
    0% {
        background-position: 1360px;
    }
    100% {
        background-position: 0;
    }
}

.bls {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: 6vh;
    color: #F55E27;
    bottom: 55vh;
    position: relative;
}
.joinButton {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    bottom: 50vh;
}



